import React from "react";
import stackoverflow from "../images/stackoverflow.png";
import github from "../images/github.png";
import linkedin from "../images/linkedin.png";
import "../styles/contact.css";

export default class Contact extends React.Component {
    render() {
        return (
            <div className="formDiv">
                <div className="formContainer">
                    <div className="formTitle">Send A Message</div>
                    <form
                        className="form"
                        action="https://formspree.io/xaypwplb"
                        method="POST">
                        <div className="nameForm">
                            <input
                                type="text"
                                name="name"
                                id="name"
                                placeholder="Name"
                            />
                        </div>
                        <div className="emailForm">
                            <input
                                type="email"
                                name="_replyto"
                                placeholder="Your email"
                            />
                        </div>
                        <div className="textForm">
                            <textarea
                                name="message"
                                rows="3"
                                placeholder="Write your message..."
                            />
                        </div>
                        <div className="formButton">
                            <button
                                type="submit"
                                className="buttonSubmit">
                                Send Message
					        </button>
                        </div>
                    </form>
                </div>
                <div className="contactContainer">
                    <div className="contactTitle">Get in Touch</div>
                    <div className="contactText">
                        Whether you want to get in touch, talk about a project collaboration, or just say hi, I'd love to hear from you.
                        Simply fill the from and send me an email.
                        </div>
                    <div className="contactImages">
                        <a
                            className="link"
                            href="https://stackoverflow.com/users/12240954/twofist"
                            target="_blank"
                            rel="noopener noreferrer">
                            <div className="imageContainer"><img src={stackoverflow} alt="" /></div>
                        </a>
                        <a
                            className="link"
                            href="https://github.com/twofist"
                            target="_blank"
                            rel="noopener noreferrer">
                            <div className="imageContainer"><img src={github} alt="" /></div>

                        </a>
                        <a
                            className="link"
                            href="https://www.linkedin.com/in/daniel-k-a9094916a"
                            target="_blank"
                            rel="noopener noreferrer">
                            <div className="imageContainer"><img className="linkedInImage" src={linkedin} alt="" /></div>
                        </a>
                    </div>
                </div>
            </div>
        );
    }
}