import React from "react";
import person from "../images/me.png";
import "../styles/about.css";

export default class About extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            languages: ["JavaScript", "TypeScript", "React", "HTML/CSS", "SQL", "MongoDB", "Git", "Java", "GDScript", "Unity"]
        }

    }

    render() {
        return (
            <div className="aboutDiv">
                <div className="skillsContainer">
                    <div className="skillsTitle">
                        <img src={person} alt="" width="200px" height="200px" />
                    </div>
                    <div className="skillsText">
                        <ul>
                            {this.state.languages.map((el) => <li key={el}>{el}</li>)}
                        </ul>
                    </div>
                </div>
                <div className="aboutContainer">
                    <div className="aboutTitle"><h3>About Me</h3></div>
                    <div className="aboutText">
                        Software Engineer who loves Engineering Software and Web development. I am passionate about creating Websites, Games and Tools to help myself.
                        <br></br>
                        <br></br>
                        With over two years of experience developing web applications using the latest front-end and back-end technologies.
                        <br></br>
                        <br></br>
                        Motivated developer with experience creating custom websites with ReactJs, JavaScript, HTML5, and CSS3. Strong collaboration skills and proven history of application development.
                        </div>
                </div>
            </div>
        );
    }
}