import React from "react";

import "../styles/navbar.css";

export default class Navbar extends React.Component {

    componentDidMount() {
        window.addEventListener('scroll', this.changeColor);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.changeColor);
    }

    changeColor() {
        const navbar = document.getElementById("mainNavbar")
        if (window.scrollY || window.pageYOffset) {
            navbar.style.backgroundColor = "black";
            navbar.style.paddingTop = "0vh";
            navbar.style.paddingBottom = "0vh";
        } else {
            navbar.style.backgroundColor = "transparent";
            navbar.style.paddingTop = "2.5vh";
            navbar.style.paddingBottom = "2.5vh";
        }
    }

    render() {
        return (
            <nav id="mainNavbar" className="navbar">
                <div className="navbarContainer">
                    <a className="home"
                        href="#home">
                        home
                        </a>
                    <a className="about"
                        href="#about">
                        about
                        </a>
                    <a className="projects"
                        href="#projects">
                        projects
                        </a>
                    <a className="contact"
                        href="#contact">
                        contact
                        </a>
                </div>
            </nav>
        )
    }
}