import React from "react";
import "../styles/portfolio.css";
import Navbar from "./navbar";
import Footer from "./footer";
import Projects from "./projects";
import Contact from "./contact";
import About from "./about";
import ReactTyped from "react-typed";
import 'react-typed/dist/animatedCursor.css';

export default class Portfolio extends React.Component {

    componentDidMount() {
        window.addEventListener("scroll", this.animation);
    }

    componentWillUnmount() {
        window.removeEventListener("scroll", this.animation);
    }

    animation() {
        const scrollTop = window.pageYOffset;
        const layers = document.getElementsByClassName("layer");
        for (const element of layers) {
            const dataSpeed = element.attributes[1].value;
            const offsetY = -(scrollTop * dataSpeed);
            const translate = "translate3d(0, " + offsetY + "px, 0)";
            element.style.transform = ("-webkit-transform", translate);
            element.style.transform = ("-moz-transform", translate);
            element.style.transform = ("-ms-transform", translate);
            element.style.transform = ("-o-transform", translate);
            element.style.transform = ("transform", translate);
        }
    }

    render() {
        return (
            <div className="background">
                <section className="header">
                    <Navbar>

                    </Navbar>
                    <div className="layer layer1" data-speed="0.10"></div>
                    <div className="layer layer2" data-speed="0.20"></div>
                    <div className="layer layer3" data-speed="0.20"></div>
                    <div className="layer layer4" data-speed="0.30"></div>
                    <div className="layer layer5" data-speed="0.30"></div>
                    <div className="layer layer6" data-speed="0.30"></div>
                    <div className="layer layer7" data-speed="0.30"></div>
                    <div className="layer layer8" data-speed="0.30"></div>
                    <div className="top-container">
                        <h1>
                            Hello, I&apos;m Twofist.
						    <br />
                            <ReactTyped strings={[
                                "I&apos;m a Front End Developer.",
                                "I&apos;m a Back End Developer.",
                                "I&apos;m a Full Stack Developer."
                            ]} typeSpeed={40} backSpeed={50} backDelay={1000} loop smartBackspace>

                            </ReactTyped>
                        </h1>
                    </div>
                </section>
                <div className="headerTransition"></div>

                <section className="description">
                    <div>
                        <scroll-page id="about"></scroll-page>
                        <About>

                        </About>
                    </div>
                    <div className="message">
                        <scroll-page id="projects"></scroll-page>
                        <Projects>

                        </Projects>
                    </div>
                    <div>
                        <scroll-page id="contact"></scroll-page>
                        <Contact>

                        </Contact>
                    </div>
                </section>
                <section className="footer">
                    <Footer>

                    </Footer>
                </section>
            </div>
        );
    }
}
