import React from "react";
import "../styles/projects.css";

export default class Projects extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            projects: [],
        }
    }

    componentDidMount() {
        this.getProjects("twofist");
    }

    async getProjects(user) {
        let response = await fetch(`https://api.github.com/users/${user}/repos?sort=created`);
        let data = await response.json();
        this.setState({ projects: data });
    }

    createProjects(el) {
        return (
            <a className="repoContainer"
                key={el.name}
                href={el.html_url}
                target="_blank"
                rel="noopener noreferrer">
                <div className="repoTitleContainer">
                    <div className="repoTitle">
                        {el.name}
                    </div>
                </div>
                <div className="repoDescription">
                    {el.description}
                </div>
                <div className="repoDetails">
                    <div className="repoLangContainer">
                        <div className="repoLanguage">{el.language}</div>
                    </div>
                    <div className="repoStarContainer">
                        <div className="repoStars">{el.stargazers_count}</div>
                    </div>
                    <div className="repoForkContainer">
                        <div className="repoForks">{el.forks_count}</div>
                    </div>
                </div>
            </a>
        )
    }

    render() {
        return (
            <main>
                <div className="projectsContainer" >
                    Repositories
                    <div id="repositories" className="repositoriesContainer">
                        {this.state.projects.length > 0 ? this.state.projects.map((el) => this.createProjects(el)) : "Projects could not be loaded from Github"}
                    </div>
                </div>
            </main>
        );
    }
}
